.menu {
  overflow: auto;

  box-sizing: border-box;
  height: 100vh;
  background-color: #fff;
  font-weight: 700;
  text-transform: uppercase;

  &__top {
    position: sticky;
    top: 0;
    z-index: 1;

    margin-bottom: 32px;

    @include on-tablet {
      margin-bottom: 0;
    }
  }

  &__nav {
    padding-bottom: 48px;
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;
    color: inherit;
    font-size: 16px;
    line-height: 22.4px;
    letter-spacing: 2px;
    text-decoration: none;
  }

  &__call-to-order {
    display: block;
    width: fit-content;
    color: inherit;
    padding-bottom: 3px;
    border-bottom: 1px solid $main-text-color;
    font-size: 16px;
    line-height: 22.4px;
    letter-spacing: 2px;
    text-decoration: none;
  }
}

.container {
  @include content-padding-inline;
}
